import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import Swal from 'sweetalert2';
import { ServerService } from '../_services/server.service';
declare var $;

@Component({
  selector: 'app-login-iimpact',
  templateUrl: './login-iimpact.component.html',
  styleUrls: ['./login-iimpact.component.css']
})

export class LoginIimpactComponent implements OnInit {

  loginForm: FormGroup;
  loading = false;
  submitted = false;
  returnUrl: string;
  error = '';

  constructor(private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private ServerService: ServerService) { }

  ngOnInit(): void {
    this.loginForm = this.formBuilder.group({
      email: ['', Validators.required],
      password: ['', Validators.required]
    });
    // get return url from route parameters or default to '/'
    this.returnUrl = '/data-entry/1/3';
    //this.byPassLogin()
    $(".selectpicker").selectpicker('refresh');
  }

  byPassLogin(){
      localStorage.setItem('user_id', '1');
      localStorage.setItem('user', '1');
      localStorage.setItem('block_name', 'test');
      localStorage.setItem('district_name', 'test');
      localStorage.setItem('state_name', 'test');
      localStorage.setItem('name', 'Vikas');
      localStorage.setItem('lc_name', 'Test LC');
      localStorage.setItem('lc_code', 'LC-00000');
      localStorage.setItem('pngo', 'Test');
      localStorage.setItem('teacher_name', 'Test TE');
      localStorage.setItem('teacher_code', 'TE-00000');
      localStorage.setItem('donor_name', 'Test');
      localStorage.setItem('supervisor_name', 'Test');
      localStorage.setItem('is_tracker', '0');
      localStorage.setItem('history', '1');
      localStorage.setItem('project', '2');
      localStorage.setItem('lang', 'en');
      this.router.navigate([this.returnUrl]);
  }

  // convenience getter for easy access to form fields
  get f() { return this.loginForm.controls; }


  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.loginForm.invalid) {
        return;
    }

    this.loading = true;

    this.ServerService.login(this.f.email.value, this.f.password.value, 2).then((data)=>{
      console.log(data['data'].user_id);
      localStorage.setItem('user_id', data['data'].user_id);
      localStorage.setItem('user', data['data'].user_id);
      localStorage.setItem('block_name', data['data'].block_name);
      localStorage.setItem('district_name', data['data'].district_name);
      localStorage.setItem('state_name', data['data'].state_name);
      localStorage.setItem('name', data['data'].lc_name);
      localStorage.setItem('lc_name', data['data'].lc_name);
      localStorage.setItem('lc_code', data['data'].lc_code);
      localStorage.setItem('pngo', data['data'].pngo);
      localStorage.setItem('teacher_name', data['data'].teacher_name);
      localStorage.setItem('teacher_code', data['data'].teacher_code);
      localStorage.setItem('donor_name', data['data'].donor_name);
      localStorage.setItem('supervisor_name', data['data'].supervisor_name);
      localStorage.setItem('is_tracker', '0');
      localStorage.setItem('history', '1');
      localStorage.setItem('project', '2');
      localStorage.setItem('lang', 'en');
      this.router.navigate([this.returnUrl]);

    }).catch((err)=>{
      console.log(err);
      this.error = err;
      this.loading = false;
    })
  }

}
